export const getRandomText = () => { 
    eval( "var word=" +  '"\\u' + (Math.round(Math.random() * 20901) + 19968).toString(16)+'"')//
    return word;
}

export const getHZ = () => {//
	let arr = hanzi;
	let pos = Math.round(Math.random() * (arr.length - 1));
	return arr[pos];
}

export const randZF = () => {//
	let arr=["a","b","c","d","e","f","g","h","i","j","k","m","n","p","q","r","s","t","u","v","w","x","y","z","2","3","4","5","6","7","8","9","#","$","%","!","*","~",""]
	let pos = Math.round(Math.random() * (arr.length - 1));
	return arr[pos];
}

export const pushHZ = (string) => {
	let arr=[];
	let len=string.length;
	for (let index = 0; index < len; index++) {
		arr.push(index)
	}
	let pos = Math.round(Math.random() * (arr.length - 1));
	let indexs=arr[pos];
	let newStrs= string.substring(0,indexs) + getHZ() + string.substring(indexs,len)
	return newStrs;
}

export const rands = (arr) => {
        var pos = Math.round(Math.random() * (arr.length - 6));
        var newArr=[]
        for(var i=0;i<5;i++){
            newArr.push(arr[pos+i])
        }
        var newPos = Math.round(Math.random() * (newArr.length - 1));
        return newArr[newPos]
    }

export const levels = (data,level,arrIndex,str,select,indexArr) => {
	let _arr=["a","b","c","d","e","f","g","h","i","j","k","m","n","p","q","r","s","t","u","v","w","x","y","z","2","3","4","5","6","7","8","9","#","$","%","!","*","~",""];//
	let randIndex1 = Math.round(Math.random() * (arrIndex.length - 1));//
	let arrtext=arrIndex[randIndex1];//
	let oldStr1=data.slice(arrtext,arrtext+1);//
	let oldStrIndex = _arr.indexOf(oldStr1);//
	_arr.splice(oldStrIndex, 1);//

	for (let _i in indexArr) {//
		let _v = indexArr[_i];
		let _preIndex=_arr.indexOf(_v)
		_arr.splice(_preIndex, 1);
	}
	
	let newStr1=rands(_arr);
	let newStrs= str.substring(0,arrtext) + newStr1 + str.substring(arrtext+1,data.length)
	
	if(level>0){
		levels(data,level-1,arrIndex,newStrs,select,indexArr)
	}else{
		select.push(newStrs);
		indexArr[arrtext].push(newStr1)
	}
}


